@import url("https://fonts.googleapis.com/css2?family=DotGothic16&family=Inter:wght@200&display=swap");

@media (min-width: 320px) and (max-width: 480px) {
  .header-list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: black;
  }
  .content-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    min-height: 900px;
  }
  .flex-headline-header {
    text-align: center;
    margin-top: 30px;
  }
  .headline-header {
    font-family: "Inter", sans-serif;
    font-size: 52px;
    font-weight: 600;
    line-height: 62px;
    letter-spacing: 0.2px;
    color: #ffffff;
  }
  .second-headline-header {
    color: #02e4c0;
  }
  .text-content-header {
    font-family: "Inter", sans-serif;
    font-size: 16px;
    line-height: 24px;
    margin-left: 20px;
    margin-right: 20px;
    text-align: center;
    color: #ffffff;
  }
  .imageContentHeader {
    width: 100%;
    height: auto;
  }
  .button-header {
    min-width: 160px;
    min-height: 55px;
    font-weight: 700;
    padding: 10px 30px 10px 30px;
    font-family: "Inter", sans-serif;
    box-shadow: 0px 4px 31px 0px #00000026;
    background-color: transparent;
    border: 1px solid white;
    color: white;
    cursor: pointer;
  }
  .first-button-header {
    background-color: #4452fe;
    color: #ffffff;
    border: 1px;
  }
  .dark-theme {
    background-color: #181818;
    color: #181818;
  }

  .light-theme {
    background-color: #f5f5f5;
    color: dark;
  }
  .content-header {
    margin-top: 20px;
  }

  .flex-headline-header {
    display: flex;
    align-items: center;
    flex-direction: column;
  }

  .headline-header {
    font-size: 34px;
    font-weight: bold;
  }
  .button-header {
    margin-top: 20px;
    padding: 10px 20px;
    font-size: 16px;
    border: none;
    border-radius: 4px;
    background-color: #007bff;
    color: #fff;
    cursor: pointer;
  }
  .dark-theme {
    background-color: #181818;
  }

  .light-theme {
    background-color: #f5f5f5;
  }

  .dark-text {
    color: #fff;
  }

  .light-text {
    color: #000000;
  }
  .switch-button {
    border-top-width: 0px;
    height: min-content;
    border-left-width: 0px;
    border-bottom-width: 0px;
    border-right-width: 0px;
  }
  .flex-header-menu {
    display: flex;
    min-width: 120px;
    justify-content: space-around;
  }
}
