@import url("https://fonts.googleapis.com/css2?family=DotGothic16&family=Inter:wght@200&display=swap");

@media (min-width: 320px) and (max-width: 480px) {
  .main-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 650px;
    justify-content: space-around;
    text-align: center;
  }
  .main-content-top {
    display: flex;
    flex-direction: column;
    min-height: 650px;
    justify-content: space-around;
  }
  .headline-main {
    font-family: "Inter", sans-serif;
    font-size: 36px;
    font-weight: 600;
    line-height: 54px;
    letter-spacing: 0.2px;
    color: #ffffff;
  }
  .main-content-middle {
    display: flex;
    margin-left: 20px;
    margin-right: 20px;
    flex-direction: column;
    justify-content: space-around;
    min-height: 450px;
  }
  .block-content-main {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    min-height: 120px;
  }
  .img-block-content {
    width: 30px;
    height: 30px;
  }
  .title-content-block {
    font-family: "Inter", sans-serif;
    font-size: 20px;
    font-weight: 900;
    line-height: 30px;
    letter-spacing: 0.1px;
    text-align: left;
    color: #ffffff;
  }
  .text-content-block {
    font-family: "Inter", sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0.2px;
    text-align: left;
    color: #ffffff;
  }
  .main-content-bottom {
    display: flex;
    margin-left: 20px;
    margin-right: 20px;
    flex-direction: column;
    justify-content: space-around;
  }

  .headline-main-bottom {
    color: #000000;
    font-weight: 900;
  }
  .text-content-main-bottom {
    font-family: "Inter", sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0.2px;
    text-align: center;
    padding-left: 15px;
    padding-right: 15px;
  }
  .block-content-main-bottom {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    min-width: 300px;
    min-height: 470px;
    border-radius: 10px;
    background-color: #ffffff;
    margin-left: 25px;
    margin-right: 25px;
    margin-top: 30px;
  }
  .dark-theme {
    background-color: #181818;
  }

  .light-theme {
    background-color: #ede9e9;
  }

  .dark-text {
    color: #fff;
  }

  .light-text {
    color: #000000;
  }
}
